import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		wPicker
	},
	data() {
		return {
			clubName: '',
			QQgroup: '',
			Venue: '',
			UserName: '',
			phone: '',
			Weixin: '',
			QQ: '',
			regionCode: '',
			regionName: '',
			introduce: '',
			imgUrl: '../../../static/logo.png',
		}
	},
	methods: {
		InputName(v) {
			this.clubName = v.detail
		},
		InputQQgroup(v) {
			this.QQgroup = v.detail
		},
		InputVenue(v) {
			this.Venue = v.detail
		},
		selectRegion() {
			this.$refs.picker1.show()
		},
		onConfirm(v) {
			console.log(v)
			this.regionName = v.result
			this.regionCode = v.checkValue[1]
		},
		InputUserName(v) {
			this.UserName = v.detail
		},
		choiceImg() {
			const that = this
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function (res) {
					console.log(res)
					that.imgUrl = res.tempFilePaths[0]
					const filepath = res.tempFilePaths[0]
					uni.showLoading({
					  title: '上传中...'
					})
					uni.uploadFile({
						url: that.$http.baseUrl + '/v1/rest/file/uploadOSS', //仅为示例，非真实的接口地址
						filePath: filepath,
						name: 'file',
						success: (uploadFileRes) => {
							uni.hideLoading();
							const resp = JSON.parse(uploadFileRes.data)
							console.log(resp);
							if(resp.status == 200) {
								that.imgUrl = resp.data[0]
							}
						},
						fail: (err) => {
							console.log(err)
						}
					});
				}
			});
		},
		inputIntrValue(v) {
			this.introduce = v.detail
		},
		InputPhone(v) {
			this.phone = v.detail
		},
		InputWeixin(v) {
			this.Weixin = v.detail
		},
		InputQQ(v) {
			this.QQ = v.detail
		},
		submit() {
			const params = {
				clubName: this.clubName,
				qqGroup: this.QQgroup,
				region: this.regionCode,
				venueName: this.Venue,
				name: this.UserName,
				phone: this.phone,
				qq: this.QQ,
				weixin: this.Weixin,
				logo: this.imgUrl,
				content: this.introduce,
				userId: uni.getStorageSync('userInfo').userId
			}
			console.log(params)
			this.$http.post({
				url: '/v1/rest/club/insertClub',
				data: params
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					uni.showModal({
						title: '提示',
						content: '申请成功，请等待审核！',
						showCancel: false,
						confirmText: '知道了',
						success: function (res) {
							uni.navigateBack()
						}
					});
				}
			})
		}
	}
}
